import React,{useContext} from "react";
import { Container, Row, Col } from "reactstrap";
import "./courses.css";
import CourseCard from "./CourseCard";
import Header from "../../components/Header/Header";
import { AppContext } from "../../context/AppContext";


const Courses = () => {
  const { courses } = useContext(AppContext);

  return (
    <>
      <Header />
      <section>
        <Container>
          <Row>
            <Col lg="12" className="mb-5">
              <div className="course__top d-flex justify-content-between align-items-center">
                <div className="course__top__left w-50">
                  <h2>Quy nh cc loi gi cha bi theo thng</h2>
                  <p>
                    Loi 1: Nhn xt chung( theo t duy, din t, kin thc) chm im, highlight nhng g n/cha n.
                  </p>
                  <p>
                    Loi 2 = Loi 1 + Tip sa nhng phn cha n + t cu hi vi nhng ngi cha bi qua meeting
                  </p>
                </div>
              </div>
            </Col>
            {courses.map((item) => (
              <Col key={item.id} lg="4" md="6" sm="6">
                <CourseCard key={item.id} item={item} />
              </Col>
            ))}
            
          </Row>
        </Container>
      </section>
    </>
  );
};

export default Courses;
