import React from "react";
import "./courses.css";
const Courses = ({ coursesData }) => {
  return (
    <div className="container">
      <h2>Cc kha hc hin c</h2>
      <table>
        <thead>
          <tr>
            <th>ID</th>
            <th>Title</th>
            <th>Lesson</th>
            <th>Cost</th>
          </tr>
        </thead>
        <tbody>
          {coursesData.map((coursesData) => (
            <tr key={coursesData.id}>
              <td>{coursesData.id}</td>
              <td>{coursesData.course}</td>
              <td>{coursesData.infor}</td>
              <td>{coursesData.cost} (VND)</td>
            </tr>
          ))}
        </tbody>
      </table>
      <p>Ch  cc kha hc  trn  c tnh ton cn thn trc khi thm vo</p>
      <button className="btn">Thm Kha Hc</button>
    </div>
  );
};

export default Courses;
